.server {
  flex: 4;
  padding: 20px;
}

.serverTitleContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.serverAddButton {
  width: 80px;
  border: none;
  padding: 5px;
  background-color: teal;
  color: white;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}

.serverTop {
  display: flex;
}

.serverTopLeft,
.serverTopRight {
  flex: 1;
}

.serverTopRight {
  padding: 20px;
  margin: 20px;
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}

.serverInfoImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 20px;
}

.serverInfoTop {
  display: flex;
  align-items: center;
}

.serverName {
  font-weight: 600;
}

.serverInfoBottom {
  margin-top: 10px;
}

.serverInfoItem {
  width: 150px;
  display: flex;
  justify-content: space-between;
}

.serverInfoValue {
  font-weight: 300;
}

.serverBottom {
  padding: 20px;
  margin: 20px;
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}

.serverForm {
  display: flex;
  justify-content: space-between;
}

.serverFormLeft {
  display: flex;
  flex-direction: column;
}

.serverFormLeft > label {
  margin-bottom: 10px;
  color: gray;
}

.serverFormLeft > input {
  margin-bottom: 10px;
  border: none;
  padding: 5px;
  border-bottom: 1px solid gray;
}

.serverFormLeft >select{
  margin-bottom: 10px;
}

.serverUploadImg{
  width: 100px;
  height: 100px;
  border-radius: 10px;
  object-fit: cover;
  margin-right: 20px;
}

.serverFormRight{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.serverUpload{
  display: flex;
  align-items: center;
}

.serverButton{
  border: none;
  padding: 5px;
  border-radius: 5px;
  background-color: darkblue;
  color:white;
  font-weight: 600;
  cursor: pointer;
}