.applicationList {
  flex: 4;
  margin-top: 20px;
width: auto;
}

.applicationListMessage {
  display: flex;
  align-items: center;
}

.applicationListImg {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}
.applicationListEdit{
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
    background-color: #3bb077;
    color: white;
    cursor: pointer;
    margin-right: 20px;
}

.applicationListDelete{
    color: red;
    cursor: pointer;
}