.serverList{
    flex: 4;
}

.serverListItem{
    display: flex;
    align-items: center;
}

.serverListImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
  }

.serverListEdit{
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
    background-color: #3bb077;
    color: white;
    cursor: pointer;
    margin-right: 20px;
}

.serverListDelete{
    color: red;
    cursor: pointer;
}

.serverAddButton{
    margin-top: 20px;
    margin-bottom: 20px;
}