.squareInfo {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.squareInfoItem {
  flex: 1;
  margin: 0px 20px;
  padding: 30px;
  border-radius: 10px;
  cursor: pointer;
  background-color:rgba(10, 13, 14, 0.75);
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}

.squareInfoTitle{
    font-size: 20px;
    color: azure;
}

.squareInfoContainer{
    margin: 10px 0px;
    display: flex;
    align-items: center;
}

.squareInfoData{
    font-size: 30px;
    font-weight: 600;
    color: white;
}

.squareIcon{
    font-size: 14px;
    margin-left: 5px;
    color: green;
}



.squareInfoSubHeading{
    font-size: 15px;
    color:lightgrey;
}