.topbar {
  width: 100%;
  height: 50px;
  background-color: rgb(40, 218, 218);
  position: sticky;
  top: 0px;

  z-index: 999;
}

.topbarWrapper {
  height: 100%;
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;

}

.title {

  font-weight: bold;
  font-size: 30px;
  color: rgb(10, 34, 74);
  cursor: pointer;
}

.topRight {
  display: flex;
  align-items: center;
  
}

.topbarIconContainer {
  position: relative;
  cursor: pointer;
  margin-right: 10px;
  color: #555;
  
  
}
.topAvatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
}

body{
    margin:0;
}



